<template>
  <div>

    <div class="loadingpage" v-if="state!='data'" @click="retry">
      <van-empty class="empty" v-if="state === 'error'" :image="neterrorImg" description="请求失败,请重试" />
      <van-empty class="empty" v-if="state === 'empty'" :image="emptyImg" description="暂无数据" />
      <van-loading class="loading" v-if="state === 'loading'" color="#0060A5" type="spinner" />

    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      emptyImg: require('../assets/common_no_data.png'),
      neterrorImg: require('../assets/im_error.png')
    }
  },

  props: {
    state: String
  },
  mounted () {

  },
  methods: {
    retry () {
      if (this.state === 'error' || this.state === 'empty') {
        this.$emit('onRetry')
      }
    }
  }

}
</script>

<style scoped>
.empty {
  margin-top: 20vh;
}
.loading {
  margin-top: 40vh;
}
</style>
