<template>
  <div id='page'>

    <Toolbar title="意见反馈" showBack />
    <div class="textfield">
      <van-field v-model="message" rows="5" autosize label="" type="textarea" placeholder="请您提出您的宝贵意见～" />
    </div>

    <div class="btn" v-on:click="onSubmit">提交</div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import { Toast } from 'vant'
import { feedback } from '@/utils/fetchData'
export default {
  data () {
    return {
      message: ''
    }
  },
  methods: {
    async onSubmit () {
      if (this.message.trim().length === 0) {
        Toast('请输入您的宝贵意见')
        return
      }

      Toast.loading({
        duration: 0,
        message: '加载中...'
      })
      const response = await feedback()

      Toast.clear()
      if (response.success) {
        Toast('反馈成功')
        this.$router.go(-1)
      } else {
        Toast(response.msg)
      }
    }
  },
  components: {
    Toolbar
  }
}
</script>

