<template>
  <div>

    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">
      <van-list v-model='loading' :error.sync="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onload" immediate-check loading-text="">
        <ExamRecordItem v-for="(item ,index) in list" :key="index" :item="item" v-on:click.native="onItemClick(index)" />
      </van-list>
    </div>
  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import ExamRecordItem from '@/components/ExamRecordItem.vue'
import { examRecordList } from '@/utils/fetchData'
import { Toast } from 'vant'
export default {
  data () {
    return {
      list: null,
      finished: false,
      error: false,
      loading: false,
      loadingState: 'loading',
      page: 0
    }
  },
  props: ['index'],
  mounted () {
    this.firstList()
  },
  methods: {
    onRetry () {
      this.firstList()
    },
    async onload () {
      if (this.list === null) {
        return
      }
      const result = await this.getList(this.index, this.page, 20)
      if (result.success) {
        this.page = this.page + 1
        this.list = this.list.concat(result.data.valueList)
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.error = true
      }
    },

    async firstList () {
      this.loadingState = 'loading'
      this.finished = false
      const result = await this.getList(this.index, this.page, 20)

      if (result.success) {
        if (result.data.totalPages === 0) {
          /// 没有数据
          this.loadingState = 'empty'
          this.finished = true
          return
        }
        this.loadingState = 'data'
        this.page = 1
        this.list = result.data.valueList
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.loadingState = 'error'
      }
    },

    async getList (type, page, pageSize) {
      const result = await examRecordList(type, page, pageSize)

      return result
    },
    onItemClick (index) {
      const item = this.list[index]
      if (item.status === 1) {
        Toast('当前考试正在进行中,请提交后再来查看')
      } else {
        console.log('查看考试详情')
      }
    }
  },
  components: {
    LoadingPage,
    ExamRecordItem
  }
}
</script>

