<template>
  <div>

    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">

      <van-list v-model='loading' :error.sync="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onload" immediate-check loading-text="">
        <StudyCourseItem v-for="(item,index) in list" :key="index" v-on:click.native="onItemClick(index)" :item="item" />
      </van-list>

    </div>
  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import StudyCourseItem from '@/components/StudyCourseItem.vue'
import { studyRecordList } from '@/utils/fetchData'
export default {
  data () {
    return {
      list: null,
      finished: false,
      error: false,
      loading: false,
      loadingState: 'loading',
      page: 0
    }
  },
  mounted () {
    this.firstList()
  },
  methods: {
    onRetry () {
      this.firstList()
    },
    async onload () {
      if (this.list === null) {
        return
      }
      const result = await this.getList(this.page, 20)
      if (result.success) {
        this.page = this.page + 1
        this.list = this.list.concat(result.data.valueList)
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.error = true
      }
    },

    async firstList () {
      this.loadingState = 'loading'
      this.finished = false
      const result = await this.getList(this.page, 20)

      if (result.success) {
        if (result.data.totalPages === 0) {
          /// 没有数据
          this.loadingState = 'empty'
          this.finished = true
          return
        }
        this.loadingState = 'data'
        this.page = 1
        this.list = result.data.valueList
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.loadingState = 'error'
      }
    },

    async getList (page, pageSize) {
      const result = await studyRecordList(page, pageSize)

      return result
    },
    onItemClick (index) {
      const item = this.list[index]
      this.$router.push({
        name: 'StudyCourseDetail',
        params: {
          item: JSON.stringify(item)
        }
      })
    }
  },

  props: {
    index: String
  },
  components: {
    LoadingPage,
    StudyCourseItem
  }
}
</script>

