<template>
  <div class="tab">
    <van-tabs v-model="active" @change="onChange" color="#0060a5" title-active-color="#0060a5" title-inactive-color="#999999" line-width="0px">
      <van-tab :title="tab1"></van-tab>
      <van-tab :title="tab2"></van-tab>
      <van-tab :title="tab3"></van-tab>
    </van-tabs>

  </div>
</template>

<script>
export default {
  name: 'ThirdSectionbar',
  data () {
    return {
      active: 0
    }
  },
  props: { tab1: String, tab2: String, tab3: String },
  methods: {
    onChange () {
      this.$emit('onSectionChange', this.active)
    }
  }
}
</script>
<style scoped>
.tab {
  width: 540px;
  z-index: 109;
  position: fixed;
}
</style>
