<template>
  <div id="page">
    <Toolbar title="课件学习" showBack />
    <div class="container">
      <div class="timePart">
        已学习{{totalTime}}分钟
      </div>
      <div class="filePart" v-if="item !== null">

        <PDFFile v-if="isPDF()" :filePath='item.url' @startStudy="startStudy" />
        <OfficeFile v-else-if="isOffice()" :filePath='item.url' />
        <VideoFile v-else-if='isVideo()' :filePath='item.url' @startStudy="startStudy" @pauseStudy="pauseStudy" />
        <AudioFile v-else-if='isAudio()' :filePath='item.url' @startStudy="startStudy" @pauseStudy="pauseStudy" />
        <div v-else>此课件不支持在线学习</div>

      </div>

      <div v-if="item !==null && (isVideo()||isAudio())">
        <div class="title">{{item.title}}</div>
        <div style="height:5px;background-color:#F2F2F2"></div>
        <div class='jj'>简介</div>
        <div class='des'>{{item.intro}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import PDFFile from '@/components/PDFFile.vue'
import OfficeFile from '@/components/OfficeFile.vue'
import VideoFile from '@/components/VideoFile.vue'
import AudioFile from '@/components/AudioFile.vue'
import { submitStudyTime } from '@/utils/fetchData'

export default {
  data () {
    return {
      item: null,
      totalTime: 0,
      recordTime: false,
      timer: null,
      periodTime: 1, /// 提交间隔时间
      countTime: 0 /// 累计计时市场
    }
  },

  mounted () {
    this.item = JSON.parse(this.$route.params.item)
    this.totalTime = this.item.learnTime
    // this.item = {
    //   fileType: 14,
    //   period: 1,
    //   title: '2016建设项目结决算审计工作交流-刘伟',
    //   intro: '中国海油2019年党风廉政建设和反腐败工作会议召开。会议深入学习习近平新时代中国特色社会主义思想，认真贯彻习近平总书记在十九届中央纪委三次全会上的重要讲话和全会精神，落实中央企业党风廉政建设和反腐败工作会安排，总结回顾2018年工作，安排部署2019年任务。集团公司党组书记、董事长杨华代表集团公司党组作讲话，要求一以贯之贯彻落实全面从严治党的方针和要求，保障党中央重大决策部署落实落地，扎扎实实推进全面从严治党、党风廉政建设和反腐败工作高质量发展。集团公司党组副书记、总经理汪东进主持会议。',
    //   url: 'https://storage.googleapis.com/gvabox/media/samples/android.mp4'
    // }

    if (this.item.period > 1) {
      this.periodTime = this.item.period
    }
  },
  methods: {
    isPDF () {
      const result = this.item.fileType === 6
      return result
    },
    isOffice () {
      const fileType = this.item.fileType
      const result = fileType === 4 || fileType === 5 || fileType === 7
      return result
    },
    isVideo () {
      const fileType = this.item.fileType
      const result = fileType === 3
      return result
    },
    isAudio () {
      const fileType = this.item.fileType
      const result = fileType === 14
      return result
    },

    pauseStudy () {
      this.recordTime = false
    },

    startStudy () {
      if (this.timer === null) {
        const _this = this
        this.timer = setInterval(() => {
          if (_this.recordTime) {
            /// 正在计时

            _this.countTime += 5
            if (_this.countTime >= _this.periodTime * 60) {
              _this.totalTime += _this.periodTime
              _this.countTime = 0
              _this.submitTime()
            }
          }
        }, 5000)
      }

      this.recordTime = true
    },

    async submitTime () {
      console.log('提交学习时间' + this.periodTime)
      submitStudyTime(this.item.projectId, this.item.id, this.periodTime)
    }

  },

  destroyed () {
    if (this.timer !== null) {
      clearInterval(this.timer)// 清除计时器
      this.timer = null// 设置为null
    }
  },

  components: {
    Toolbar,
    PDFFile,
    OfficeFile,
    VideoFile,
    AudioFile
  }
}
</script>
