
<!--学习 项目 item-->
<template>

  <div class="item">
    <div class="title">{{item.title}}</div>
    <div class="content">
      <div class="file" v-bind:style='{color:fileColor}'>{{fileName}}</div>
      <div class="status" v-bind:style='{color:statusLableColor}'>{{statusLable}}</div>
      <div class="time">{{item.status === 0?"":"已学习"+item.learnTime+"分钟"}}</div>
      <div class="btn" v-if="showStudyBtn">{{item.status !== 0 ? "继续学习" : "开始学习"}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showStudyBtn: true
    }
  },
  props: ['item', 'showBtn'],
  mounted () {
    if (this.showBtn === 'false') {
      this.showStudyBtn = false
    }
  },
  computed: {
    fileName () {
      const fileType = this.item.fileType
      if (fileType === 3) {
        return 'video'
      }
      if (fileType === 4) {
        return 'doc'
      }
      if (fileType === 5) {
        return 'excel'
      }
      if (fileType === 6) {
        return 'pdf'
      }
      if (fileType === 7) {
        return 'ppt'
      }
      if (fileType === 14) {
        return 'audio'
      }
      return '未知'
    },
    fileColor () {
      const fileType = this.item.fileType
      if (fileType === 3) {
        return '#673AB7'
      }
      if (fileType === 4) {
        return '#2196F3'
      }
      if (fileType === 5) {
        return '#388E3C'
      }
      if (fileType === 6) {
        return '#FF6558'
      }
      if (fileType === 7) {
        return '#B71C1C'
      }
      if (fileType === 14) {
        return '#EF6C00'
      }
      return '#0060A5'
    },
    statusLable () {
      const status = this.item.status
      if (status === 0) {
        return '未学习'
      }
      if (status === 5) {
        return '学习中'
      }
      if (status === 10) {
        return '已完成'
      }
      return ''
    },
    statusLableColor () {
      const status = this.item.status
      if (status === 0) {
        return '#A50000'
      }
      if (status === 5) {
        return '#A50000'
      }
      if (status === 5) {
        return '#999999'
      }
      return '#999999'
    }

  }
}
</script>

