import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Launcher from '../views/Launcher.vue'
import Main from '../views/Main.vue'
import StudyCourseList from '../views/study/StudyCourseList.vue'
import ExamCourseList from '../views/exam/ExamCourseList.vue'
import store from '@/store/index.js'
import Feedback from '@/views/mine/Feedback.vue'
import Integral from '@/views/mine/Integral.vue'
import UserInfo from '@/views/mine/UserInfo.vue'
import EditPW from '@/views/mine/EditPW.vue'
import MyRecord from '@/views/mine/MyRecord.vue'
import StudyCourseDetail from '@/views/study/StudyCourseDetail.vue'
import Test from '@/views/Test.vue'
import StudySearch from '../views/study/StudySearch.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Launcher',
    component: Launcher,
    meta: {
      title: 'index'
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: '登录'
    }
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/main',
    name: 'Main',
    component: Main,
    meta: {
      title: '主页',
      keepAlive: true
    }
  },
  {
    path: '/studyCourseList',
    name: 'StudyCourseList',
    component: StudyCourseList,
    meta: {
      title: '课件列表'
    }
  },
  {
    path: '/examCourseList',
    name: 'ExamCourseList',
    component: ExamCourseList,
    meta: {
      title: '专题考试'
    }
  },
  {
    path: '/feedback',
    name: 'Feedback',
    component: Feedback,
    meta: {
      title: '意见反馈'
    }
  },
  {
    path: '/integral',
    name: 'Integral',
    component: Integral,
    meta: {
      title: '积分排名'
    }
  },
  {
    path: '/userInfo',
    name: 'UserInfo',
    component: UserInfo,
    meta: {
      title: '个人信息'
    }
  },
  {
    path: '/editPW',
    name: 'EditPW',
    component: EditPW,
    meta: {
      title: '修改密码'
    }
  },
  {
    path: '/myRecord',
    name: 'MyRecord',
    component: MyRecord,
    meta: {
      title: '我的记录'
    }
  },
  {
    path: '/studyCourseDetail/:item',
    name: 'StudyCourseDetail',
    component: StudyCourseDetail,
    meta: {
      title: '课件学习'
    }
  },
  {
    path: '/studySearch',
    name: 'StudySearch',
    component: StudySearch,
    meta: {
      keepAlive: true,
      title: '课件搜索'
    }
  },
  {
    path: '/test',
    component: Test
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes

})

router.beforeEach(function (to, from, next) {
  const toPath = to.path

  if (to.meta.title) {
    document.title = to.meta.title
  }

  if (toPath === '/test') {
    next()
    return
  }

  if (toPath.toLowerCase() === '/main') {
    store.dispatch('addKeepAlive', 'Main')
  }

  /// 没有登录
  if ((store.state.token === null || store.state.token === '') && (store.state.userId === null || store.state.userId === '')) {
    if (toPath !== '/login') {
      next({
        name: 'Login',
        replace: true
      })
      return
    }
  } else {
    if ((toPath === '/login' || toPath === '/') && toPath !== '/main') {
      next({
        name: 'Main',
        replace: true
      })
      return
    }
  }
  next()
})

export default router
