<template>
  <div class="progress">

    <div class="progressdown"></div>
    <div class="progressup" v-bind:style="{width:myprogress}"></div>
    <div class="progresscount">{{progress !=null?progress:0}}%</div>

  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  props: {
    progress: Number
  },
  mounted () {
    var that = this
    window.onresize = () => {
      return (() => {
        if (this.progress == null) {
          this.progress = 0
        }
        that.myprogress = (that.progress / 540) * 300 + 'vw'
      })()
    }
  },
  activated () {

  },
  computed: {
    myprogress () {
      return (this.progress / 540) * 300 + 'vw'
    }
  }

}
</script>

<style scoped>
.progress {
  flex: 1;
  width: 300px;
  position: relative;
  margin-bottom: 16px;
}
.progressdown {
  width: 300px;
  height: 14px;
  z-index: 99;
  position: absolute;
  background: rgba(240, 240, 240, 1);
  border-radius: 9px;
}

.progressup {
  height: 14px;
  z-index: 100;

  position: absolute;
  background: #77c6ff;
  border-radius: 9px;
}
.progresscount {
  width: 40px;
  height: 25px;
  font-size: 12px;
  right: 0;
  margin-right: -50px;
  top: -5px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(153, 153, 153, 1);
  line-height: 25px;
  position: absolute;
}
</style>
