<template>
  <div>

    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">
      <div class="container">
        <div class="headerPart" v-on:click="onHeaderClick">
          <img :src="header" class="round_icon" alt="">
          <div class="an">
            <div class="name">{{userName}}</div>
            <div class="account">账号: {{account}}</div>
          </div>
          <van-icon name="arrow" color="#CCCCCC" size="30px" style="margin-right:15px" />
        </div>

        <SingleTextItem title="积分排名" @onItemClick="onItemClick1" />
        <div style="margin-top:2px"></div>
        <SingleTextItem title="我的记录" @onItemClick="onItemClick2" />
        <div style="margin-top:2px"></div>
        <SingleTextItem title="意见反馈" @onItemClick="onItemClick3" />
      </div>
    </div>
  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import SingleTextItem from '@/components/SingleTextItem.vue'
import { userInfo } from '@/utils/fetchData'
export default {
  data () {
    return {
      loadingState: 'loading',
      header: require('@/assets/head_defalut.png'),
      userName: '',
      account: ''
    }
  },
  mounted () {
    this.getUserInfo()
  },
  methods: {
    onRetry () {
      this.getUserInfo()
    },

    async getUserInfo () {
      this.loadingState = 'loading'
      const response = await userInfo()
      if (response.success) {
        this.loadingState = 'data'
        this.$store.commit('userInfo', response.data)
        this.header = response.data.avatar
        this.userName = response.data.username
        this.account = response.data.account
      } else {
        this.loadingState = 'error'
      }
    },
    onHeaderClick () {
      this.$router.push('UserInfo')
    },

    onItemClick3 () {
      this.$router.push('Feedback')
    },
    onItemClick1 () {
      this.$router.push('Integral')
    },
    onItemClick2 () {
      this.$router.push('MyRecord')
    }
  },
  components: {
    LoadingPage,
    SingleTextItem
  }
}
</script>

