<template>
  <div>
    <div id="page">
      <form action="/">
        <van-search v-model="inputText" placeholder="请输入搜索关键词" @search="onSearch" background="#0060A5" shape="round" show-action>
          <template #action>
            <div @click="onSearch" style="color:white;font-size:16px;margin-right:10px;margin-left:10px" class="searchBtn">搜索 </div>
          </template>
          <template #left>
            <van-icon @click="onBack" name="arrow-left" size="24" color="white" style="margin-right:10px"></van-icon>
          </template>
        </van-search>
      </form>
      <div class="container">
        <LoadingPage :state="loadingState" />
        <div v-if="loadingState==='data'">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了">
            <StudyCourseItem v-for="(item,index) in list" :key="index" :item="item" v-on:click.native="onItemClick(index)" showBtn=false />
          </van-list>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import StudyCourseItem from '@/components/StudyCourseItem.vue'
import { Toast } from 'vant'
import { studySearchList } from '@/utils/fetchData'
export default {
  name: 'Search',
  methods: {
    async onSearch (val) {
      const text = this.inputText
      if (text.trim().length === 0) {
        Toast('请输入您想要搜索的内容')
        return
      }
      this.firstList()
    },
    onBack () {
      this.$router.go(-1)
    },
    async onload () {
      const result = await this.getList(this.index, this.page, 20)
      if (result.success) {
        this.page = this.page + 1
        this.list = this.list.concat(result.data.valueList)
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.error = true
      }
    },

    async firstList () {
      this.loadingState = 'loading'
      this.finished = true
      const result = await this.getList(this.inputText, this.page, 100)

      if (result.success) {
        if (result.data.totalPages === 0) {
          /// 没有数据
          console.log('empty')
          this.loadingState = 'empty'
          return
        }

        this.loadingState = 'data'
        this.page = 1
        this.list = result.data.valueList
        // if (result.data.totalPages === result.data.currentPage) {
        this.finished = true
        // }
      } else {
        Toast(result.msg)
        this.loadingState = 'idel'
      }
    },

    async getList (name, page, pageSize) {
      const result = await studySearchList(name, page, pageSize)

      return result
    },
    onItemClick (index) {
      const item = this.list[index]
      this.$router.push({
        name: 'StudyCourseDetail',
        params: {
          item: JSON.stringify(item)
        }
      })
    }
  },
  data () {
    return {
      inputText: '',
      list: null,
      loading: false,
      finished: true,
      loadingState: 'idel'
    }
  },
  components: {
    LoadingPage,
    StudyCourseItem
  },
  mounted () {
    this.$store.dispatch('addKeepAlive', 'Search')
  },

  beforeRouteLeave (to, from, next) {
    if (to.name !== 'StudyCourseDetail') {
      /// 销毁
      this.$store.dispatch('removeKeepAlive', 'Search')
    }
    next()
  }
}
</script>

<style scoped>
#page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  width: 100%;
  flex: 1;
  overflow: auto;
}
</style>
