<template>
  <div class="studyListPage">

    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">
      <van-list class="studyList" v-model='loading' :error.sync="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onload" immediate-check loading-text="">
        <StudyItem v-for="(item ,index) in studyList" :key="index" :item="item" v-on:click.native="onItemClick(index)" :index='index' />
      </van-list>
    </div>
  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import StudyItem from '@/components/StudyItem.vue'
import { studyList } from '@/utils/fetchData'
export default {

  data () {
    return {
      studyList: null,
      finished: false,
      error: false,
      loading: false,
      loadingState: 'data',
      page: 0,
      clickItem: -1
    }
  },
  props: ['index'],
  mounted () {
    this.watchUpdateItem()
    this.firstStudyList()
    /// / 当数据更新是进行更新
  },

  beforeDestroy () {
    this.removeWatch()
  },

  methods: {
    onRetry () {
      this.firstStudyList()
    },

    async onload () {
      const result = await this.getStudyList(this.index, this.page, 20)
      if (result.success) {
        this.page = this.page + 1
        this.studyList = this.studyList.concat(result.data.valueList)
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.error = true
      }
    },

    async firstStudyList () {
      this.loadingState = 'loading'
      this.finished = false
      const result = await this.getStudyList(this.index, this.page, 20)

      if (result.success) {
        if (result.data.totalPages === 0) {
          /// 没有数据
          this.loadingState = 'empty'
          return
        }
        this.loadingState = 'data'
        this.page = 1
        this.studyList = result.data.valueList
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.loadingState = 'error'
      }
    },

    async getStudyList (type, page, pageSize) {
      const result = await studyList(type, page, pageSize)
      return result
    },

    onItemClick (index) {
      this.clickItem = index
      this.$router.push({
        name: 'StudyCourseList',
        query: {
          type: this.index,
          id: this.studyList[index].id
        }
      })
    },
    watchUpdateItem () {
      if (this.index === '0') {
        this.$bus.$on('updateBXItem', (item) => {
          if (this.studyList !== null && this.clickItem >= 0) {
            var study = this.studyList[this.clickItem]

            if (study !== null && study.id === item.id) {
              study.taskCount = item.taskCount
              study.completCount = item.completCount

              this.studyList.splice(this.clickItem, 1, study)
            }
          }
        })
      } else if (this.index === '1') {
        this.$bus.$on('updateXXItem', (item) => {
          if (this.studyList !== null && this.clickItem >= 0) {
            var study = this.studyList[this.clickItem]

            if (study !== null && study.id === item.id) {
              study.taskCount = item.taskCount
              study.completCount = item.completCount
              this.$set(this.studyList, this.clickItem, study)
            }
          }
        })
      }
    },
    removeWatch () {
      if (this.index === '0') {
        this.$bus.$off('updateBXItem')
      } else if (this.index === '1') {
        this.$bus.$off('updateXXItem')
      }
    }
  },

  components: {
    LoadingPage,
    StudyItem
  }
}
</script>

