<template>
  <div>

    <video :src="filePath" controls="controls" class="video" preload>
      浏览器不支持播放此视频
    </video>

  </div>
</template>

<script>

export default {
  components: {

  },
  props: ['filePath'],

  mounted () {
    this.watchVideoState()
  },
  methods: {
    watchVideoState () {
      const _this = this
      const $video = document.getElementsByTagName('video')[0]// 获取音乐DOM节点

      $video.addEventListener('playing', function () { // 播放状态Doing
        _this.playState = true// 播放状态
        console.log('播放中')
        _this.$emit('startStudy')
      }); $video.addEventListener('pause', function () { // 暂停状态Doing
        _this.playState = false// 暂停状态
        console.log('暂停播放')
        _this.$emit('pauseStudy')
      })
    }
  },
  beforeDestroy () {

  }

}

</script>

<style scoped>
.video {
  height: auto;
  width: 540px;
}
</style>
