<template>

  <div>
    <van-dialog v-model="showDialog" title="提示" width="280px" message="本次考试已经结束,重新开始考试将清除已有作答结果" closeOnPopstate closeOnClickOverlay :show-confirm-button="showDialogBtn">
      <div class="dialog_content">本次考试已结束,重新开始考试将清除已有作答结果</div>
      <div class="line"></div>
      <div class="cxks" v-on:click="oncxksClick">重新开始</div>
      <div class="line"></div>
      <div class="ckxq" v-on:click="onckxqClick">查看详情</div>
      <div class="line"></div>
      <div class="qx" v-on:click="onqxClick">取消</div>

    </van-dialog>

    <div id="page">
      <Toolbar :title="title" showBack />
      <div class="container">
        <LoadingPage :state="loadingState" @onRetry="onRetry" />
        <div v-if="loadingState==='data'">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了">
            <ExamCourseItem v-for="(item,index) in list" :key="index" :item="item" v-on:click.native="onItemClick(index)" />
          </van-list>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import LoadingPage from '@/components/LoadingPage.vue'
import ExamCourseItem from '@/components/ExamCourseItem.vue'
import { examCourseList } from '@/utils/fetchData'
export default {
  data () {
    return {
      title: '',
      list: null,
      loading: false,
      finished: true,
      loadingState: 'loading',
      showDialog: false,
      showDialogBtn: false
    }
  },
  mounted () {
    this.id = this.$route.query.id
    this.type = this.$route.query.type /// 0 考试 1练习
    this.title = '专题' + (this.type === '0' ? '考试' : '练习')
    this.getList()
  },
  methods: {
    onRetry () {
      this.getList()
    },

    async getList () {
      this.loadingState = 'loading'
      const result = await examCourseList(this.id, this.type)
      if (result.success) {
        if (result.data.examList.length === 0) {
          this.loadingState = 'empty'
        } else {
          this.loadingState = 'data'
          this.list = result.data.examList

          var updateData = {}
          updateData.id = this.id
          updateData.taskCount = result.data.taskCount
          updateData.completCount = result.data.completCount

          if (this.type === '0') {
            this.$bus.$emit('updateKSItem', updateData)
          } else if (this.type === '1') {
            this.$bus.$emit('updateLXItem', updateData)
          }
        }
      } else {
        this.loadingState = 'error'
      }
    },
    onItemClick (index) {
      const item = this.list[index]
      const status = item.status
      if ((status === 2 || status === 3) && item.redo === 1) {
        this.showDialog = true
      }
    },
    oncxksClick () {
      this.showDialog = false
    },
    onckxqClick () {
      this.showDialog = false
    },
    onqxClick () {
      this.showDialog = false
    }
  },

  components: {
    LoadingPage,
    Toolbar,
    ExamCourseItem
  }
}
</script>

