<template>
  <div>

    <audio :src="filePath" controls="controls" class="audio" preload>
      浏览器不支持播放此音频
    </audio>

  </div>
</template>

<script>

export default {
  components: {

  },
  props: ['filePath'],

  mounted () {
    this.watchAudioState()
  },
  methods: {
    watchAudioState () {
      const _this = this
      const $audio = document.getElementsByTagName('audio')[0]// 获取音乐DOM节点

      $audio.addEventListener('playing', function () { // 播放状态Doing
        _this.playState = true// 播放状态
        console.log('播放中')
        _this.$emit('startStudy')
      }); $audio.addEventListener('pause', function () { // 暂停状态Doing
        _this.playState = false// 暂停状态
        console.log('暂停播放')
        _this.$emit('pauseStudy')
      })
    }
  },
  beforeDestroy () {

  }

}

</script>

<style scoped>
.audio {
  margin-top: 40px;
  width: 500px;
}
.audio:focus {
  outline: none;
}
</style>
