<template>
  <div id="page">
    <Toolbar title="课件列表" showBack />
    <div class="container" ref="viewBox">
      <LoadingPage :state="loadingState" @onRetry="onRetry" />
      <div v-if="loadingState==='data'">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了">
          <StudyCourseItem v-for="(item,index) in list" :key="index" :item="item" v-on:click.native="onItemClick(index)" />
        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import LoadingPage from '@/components/LoadingPage.vue'
import StudyCourseItem from '@/components/StudyCourseItem.vue'
import { studyCourseList } from '@/utils/fetchData'
export default {
  data () {
    return {
      list: null,
      loading: false,
      finished: true,
      loadingState: 'loading'
    }
  },
  mounted () {
    this.id = this.$route.query.id
    this.type = this.$route.query.type /// 0 必学 1选学
    this.getList()

    // this.box = this.$refs.viewBox
    // this.box.addEventListener('scroll', () => {
    //   const aa = this.box.scrollTop
    //   console.log(aa)
    // })
  },
  methods: {
    onRetry () {
      this.getList()
    },

    async getList () {
      this.loadingState = 'loading'
      const result = await studyCourseList(this.id, this.type)
      if (result.success) {
        if (result.data.length === 0) {
          this.loadingState = 'empty'
        } else {
          this.loadingState = 'data'
          this.list = result.data.bookList

          var updateData = {}
          updateData.id = this.id
          updateData.taskCount = result.data.taskCount
          updateData.completCount = result.data.completCount

          if (this.type === '0') {
            this.$bus.$emit('updateBXItem', updateData)
          } else if (this.type === '1') {
            this.$bus.$emit('updateXXItem', updateData)
          }
        }
      } else {
        this.loadingState = 'error'
      }
    },
    onItemClick (index) {
      const item = this.list[index]
      this.$router.push({
        name: 'StudyCourseDetail',
        params: {
          item: JSON.stringify(item)
        }
      })
    }

  },

  components: {
    LoadingPage,
    Toolbar,
    StudyCourseItem
  }
}
</script>

<style scoped>
#page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  width: 100%;
  flex: 1;
  overflow: auto;
}
</style>
