
<!--考试 项目 item-->
<template>

  <div class="item">
    <div class="title">{{item.title}}</div>
    <div class="content">
      <div class="status">时长: {{item.totalTime}}分钟 学分: {{item.passCredit}}分 合格成绩: {{item.passResult}}分</div>
      <div class="btn">{{btnText}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['item'],

  methods: {

  },
  computed: {
    btnText () {
      var t2 = this.type === 0 ? '考试' : '练习'
      var t1 = '开始'
      if (this.item.status === 0) {
        t1 = '开始'
      } else if (this.item.status === 1) {
        t1 = '继续'
      } else if (this.item.status === 2 || this.item.status === 3) {
        if (this.item.redo === 1) {
          t1 = '重新开始'
        } else {
          t1 = '查看成绩'
        }
        t2 = ''
      }
      return t1 + t2
    }

  }
}
</script>

