<template>
  <div id="tt">
    <van-tabbar v-model="active"
                class="tabbar"
                @change="onIndexChange">
      <van-tabbar-item>
        <span>学习</span>
        <template #icon="props">
          <img :src="props.active ? icon1.active : icon1.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item>
        <span>考试</span>
        <template #icon="props">
          <img :src="props.active ? icon2.active : icon2.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item>
        <span>我的</span>
        <template #icon="props">
          <img :src="props.active ? icon3.active : icon3.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>

export default {
  data: function () {
    return {
      active: 0,
      icon1: {
        active: require('../assets/icon_study_s.png'),
        inactive: require('../assets/icon_study.png')
      },
      icon2: {
        active: require('../assets/icon_exam_s.png'),
        inactive: require('../assets/icon_exam.png')
      },
      icon3: {
        active: require('../assets/icon_my_s.png'),
        inactive: require('../assets/icon_my.png')
      }
    }
  },
  methods: {
    onIndexChange (index) {
      /// 子组件把事件传递出去
      this.$emit('onTabChange', index)
    }
  }

}

</script>
<style scoped>
#tt /deep/ .van-tabbar--fixed {
  left: 1;
  position: relative;
  margin: 0 auto;
}
.tabbar {
  margin: 0 auto;
  width: 540px;
}
</style>
