<template>
  <div id="page">
    <Toolbar title="积分排名" showBack />
    <div class="container">
      <LoadingPage :state="loadingState" @onRetry="onRetry" />
      <div v-if="loadingState==='data'">
        <van-list v-model="loading" :finished="finished" finished-text="">

          <IntegralItem v-for="(item, index) in list" v-bind:item="item" v-bind:index="index" v-bind:key="item" />

        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import { integralList } from '@/utils/fetchData'
import LoadingPage from '@/components/LoadingPage.vue'
import IntegralItem from '@/components/IntegralItem.vue'
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: true,
      loadingState: 'loading'
    }
  },
  mounted () {
    this.getIntegralList()
  },
  methods: {
    onRetry () {
      this.getIntegralList()
    },
    async getIntegralList () {
      this.loadingState = 'loading'
      const response = await integralList()
      if (response.success) {
        this.loadingState = 'data'
        this.list = response.data
      } else {
        this.loadingState = 'error'
      }
    }
  },

  components: {
    LoadingPage,
    Toolbar,
    IntegralItem
  }
}
</script>

<style scoped>
#page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  width: 100%;
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
}
</style>
