<template>
  <div id="login">
    <img class="loginBG" src="../assets/login_bg.png">
    <div class="loginContainer">
      <label class="loginLabel">登录</label>
      <div class="accountDiv">
        <img class="loginIcon" src="../assets/icon_phone1.png">
        <input class="loginIP" v-model="account" placeholder="请输入您的账号">
      </div>
      <div class="line" />

      <div class="pwDiv">
        <img class="pwIcon" src="../assets/icon_lock.png">
        <input class="loginIP" v-model="password" placeholder="请输入您的密码" type="password">
      </div>
      <div class="line" />

      <van-button class="loginBtn" v-on:click="onLoginClick">登陆</van-button>

    </div>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { login } from '../utils/fetchData'
import { Toast } from 'vant'
export default {
  name: 'Login',
  inject: ['reload'],

  data: function () {
    return {
      account: 'ceshi',
      password: 'a123456'

    }
  },
  methods: {

    async onLoginClick () {
      if (this.account.length === 0) {
        /// 用户名不能为空
        Toast('请输入您的账号')
        return
      }
      if (this.password.length === 0) {
        /// 密码不能为空
        Toast('请输入您的密码')
        return
      }
      Toast.loading({
        duration: 0,
        message: '加载中...'
      })
      const response = await login(this.account, this.password)
      Toast.clear()
      if (response.success) {
        this.$store.commit('userInfo', response.data)
        this.$store.commit('token', response.data.token)
        this.$router.replace('Main')
      } else {
        Toast(response.msg)
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('clearAllKeepAlive')
      vm.$bus.$off()
      const fromPath = from.path
      if (fromPath !== '/' && !fromPath.startsWith('/login')) {
        vm.reload()
      }
    })
  }

}
</script>

