<template>
  <div>

    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">
      <van-list v-model='loading' :error.sync="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onload" immediate-check loading-text="">
        <ExamItem v-for="(item,index) in list" :key="index" :isExam='index==="1"' v-on:click.native="onItemClick(index)" :item="item" />
      </van-list>
    </div>
  </div>
</template>

<script>
import LoadingPage from '@/components/LoadingPage.vue'
import ExamItem from '@/components/ExamItem.vue'
import { examList } from '@/utils/fetchData'
export default {
  data () {
    return {
      list: null,
      finished: false,
      error: false,
      loading: false,
      loadingState: 'loading',
      page: 0,
      clickItem: -1
    }
  },
  mounted () {
    this.watchUpdateItem()
    this.firstList()
  },
  beforeDestroy () {
    this.removeWatch()
  },
  methods: {
    onRetry () {
      this.firstList()
    },
    async onload () {
      const result = await this.getList(this.index, this.page, 20)
      if (result.success) {
        this.page = this.page + 1
        this.list = this.list.concat(result.data.valueList)
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.error = true
      }
    },

    async firstList () {
      this.loadingState = 'loading'
      this.finished = false
      const result = await this.getList(this.index, this.page, 20)

      if (result.success) {
        if (result.data.totalPages === 0) {
          /// 没有数据
          this.loadingState = 'empty'
          return
        }
        this.loadingState = 'data'
        this.page = 1
        this.list = result.data.valueList
        if (result.data.totalPages === result.data.currentPage) {
          this.finished = true
        }
      } else {
        this.loadingState = 'error'
      }
    },

    async getList (type, page, pageSize) {
      const result = await examList(type, page, pageSize)

      return result
    },
    onItemClick (index) {
      this.clickItem = index
      this.$router.push({
        name: 'ExamCourseList',
        query: {
          type: this.index,
          id: this.list[index].id
        }
      })
    },
    watchUpdateItem () {
      if (this.index === '0') {
        this.$bus.$on('updateKSItem', (item) => {
          if (this.list !== null && this.clickItem >= 0) {
            var exam = this.list[this.clickItem]

            if (exam !== null && exam.id === item.id) {
              exam.taskCount = item.taskCount
              exam.completCount = item.completCount

              this.list.splice(this.clickItem, 1, exam)
            }
          }
        })
      } else if (this.index === '1') {
        this.$bus.$on('updateLXItem', (item) => {
          if (this.list !== null && this.clickItem >= 0) {
            var exam = this.list[this.clickItem]

            if (exam !== null && exam.id === item.id) {
              exam.taskCount = item.taskCount
              exam.completCount = item.completCount

              this.list.splice(this.clickItem, 1, exam)
            }
          }
        })
      }
    },
    removeWatch () {
      if (this.index === '0') {
        this.$bus.$off('updateKSItem')
      } else if (this.index === '1') {
        this.$bus.$off('updateLXItem')
      }
    }
  },

  props: ['index'],
  components: {
    LoadingPage,
    ExamItem
  }
}
</script>
