<template>
  <div>
    <LoadingPage :state="loadingState" @onRetry="onRetry" />
    <div v-if="loadingState==='data'">
      <pdf v-for="i in numPages" :key="i" :src="src" :page="i" style="display: inline-block; width: 100%"></pdf>
    </div>
  </div>
</template>

<script>
import pdf from 'vue-pdf'
import LoadingPage from '@/components/LoadingPage'

export default {
  components: {
    pdf,
    LoadingPage
  },
  data () {
    return {
      src: '',
      loadingState: 'loading',
      numPages: undefined
    }
  },
  props: ['filePath'],
  mounted () {
    this.getPDF()
  },
  methods: {
    onRetry () {
      this.getPDF()
    },
    getPDF () {
      //  const test = 'http://file.dakawengu.com/file/2018-05-29/20180527-tianfeng.pdf'

      // this.src = pdf.createLoadingTask(this.filePath)
      this.src = pdf.createLoadingTask('http://file.dakawengu.com/file/2018-05-29/20180527-tianfeng.pdf')
      this.src.promise.then(pdf => {
        this.numPages = pdf.numPages
        this.loadingState = 'data'
        this.$emit('startStudy')
      }).catch(() => {
        this.loadingState = 'error'
      })
    }
  }

}
</script>
