<template>
  <div>
    <div id="main">
      <Toolbar :title="title" :showSearch="tabIndex===0" />

      <div class="pages" ref="viewBox">
        <Study v-show="tabIndex===0" />
        <Exam v-show="tabIndex===1" />
        <Mine v-show="tabIndex === 2" />
      </div>
      <Tabbar @onTabChange="onTabChange" />
    </div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import Tabbar from '@/components/Tabbar.vue'
import Study from '@/views/study/Study.vue'
import Exam from '@/views/exam/Exam.vue'
import Mine from '@/views/mine/Mine.vue'
export default {
  name: 'Main',
  data () {
    return {
      title: '学习',
      tabIndex: 0,
      studyIndex: 0, /// 0 必修 1 选修
      examIndex: 0, /// 0 考试 1 练习
      study0Top: 0,
      study1Top: 0,
      exam0Top: 0,
      exam1Top: 0
    }
  },
  mounted () {
    this.scrollListener()
    this.watchSubIndex()
  },

  activated () {
    this.scrollTop()
  },
  beforeDestroy () {
    this.removeSubIndex()
    this.box.removeEventListener('scroll')
  },

  methods: {
    onTabChange (index) {
      if (index === 0) {
        this.title = '学习'
      } else if (index === 1) {
        this.title = '考试'
      } else if (index === 2) {
        this.title = '我的'
      }
      this.tabIndex = index
      this.scrollTop()
    },
    scrollTop () {
      var scrollTop = 0
      if (this.tabIndex === 0) {
        if (this.studyIndex === 0) {
          scrollTop = this.study0Top
        } else {
          scrollTop = this.study1Top
        }
      }
      if (this.tabIndex === 1) {
        if (this.examIndex === 0) {
          scrollTop = this.exam0Top
        } else {
          scrollTop = this.exam1Top
        }
      }
      this.$nextTick(() => {
        this.box.scrollTop = scrollTop
        console.log('scroll top ' + scrollTop)
      })
    },

    watchSubIndex () {
      this.$bus.$on('studyIndexClick', (index) => {
        this.studyIndex = index
        this.scrollTop()
      })
      this.$bus.$on('examIndexClick', (index) => {
        this.examIndex = index
        this.scrollTop()
      })
    },
    removeSubIndex () {
      this.$bus.$off('studyIndexClick')
      this.$bus.$off('examIndexClick')
    },

    scrollListener () {
      this.box = this.$refs.viewBox
      this.box.addEventListener('scroll', () => {
        const top = this.box.scrollTop
        if (this.tabIndex === 0) {
          if (this.studyIndex === 0) {
            this.study0Top = top
          } else {
            this.study1Top = top
          }
        }
        if (this.tabIndex === 1) {
          if (this.examIndex === 0) {
            this.exam0Top = top
          } else {
            this.exam1Top = top
          }
        }
      })
    }
  },
  components: {
    Toolbar,
    Tabbar,
    Study,
    Exam,
    Mine
  }
}
</script>

<style scoped>
#main {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.pages {
  width: 100%;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
}

.container::-webkit-scrollbar {
  display: none;
  width: 0;
}
</style>
