<template>
  <div id='page'>

    <Toolbar title="修改密码" showBack />
    <van-field rows="1" label="当前账号:" label-align="right" readonly :placeholder="account" label-width="80px" />
    <van-field rows="1" label="当前密码:" v-model="oldPW" label-align="right" type="password" placeholder="请输入您当前的密码" label-width="80px" />
    <van-field rows="1" label="新密码:" v-model="newPW" label-align="right" type="password" placeholder="请输入您的新密码" label-width="80px" />
    <van-field rows="1" label="确认密码:" v-model="sureNewPW" label-align="right" type="password" placeholder="请确认您的新密码" label-width="80px" />

    <div class="btn" v-on:click="onSubmit">提交</div>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import { Toast } from 'vant'
import { editpw } from '@/utils/fetchData'
export default {
  data () {
    return {
      account: '',
      oldPW: '',
      newPW: '',
      sureNewPW: ''
    }
  },
  mounted () {
    this.account = this.$store.state.userInfo.account
  },

  methods: {
    async onSubmit () {
      if (this.oldPW.trim().length === 0) {
        Toast('请输入您当前的密码')
        return
      }
      if (this.newPW.trim().length === 0) {
        Toast('请输入您的新密码')
        return
      }
      if (this.sureNewPW.trim().length === 0) {
        Toast('请确认您的新密码')
        return
      }

      if (this.sureNewPW.trim() !== this.newPW.trim()) {
        Toast('两次密码输入不一致')
        return
      }

      Toast.loading({
        duration: 0,
        message: '加载中...'
      })
      const response = await editpw(this.oldPW, this.newPW)
      Toast.clear()
      if (response.success) {
        Toast('密码修改成功')
        this.$router.go(-1)
      } else {
        Toast(response.msg)
      }
    }
  },
  components: {
    Toolbar
  }
}
</script>

