#page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.btn {
  position: absolute;
  bottom: 0;
  width: 540px;
  height: 66px;
  background: rgba(32, 139, 216, 1);
  font-size: 22px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 66px;
}
</style>
