import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

var state = {
  isMobile: false, // 判断是pc还是mobile
  isInApp: false, // 是否集成到App中
  token: null, // token用于pc验证
  userId: null, // 用户ID
  userInfo: null, // 用户信息
  keepAlivePages: []/// 缓存页面
}

var mutations = {
  isMobile (state, isMobile) {
    state.isMobile = isMobile
  },

  isInApp (state, isInApp) {
    state.isInApp = isInApp
  },
  token (state, token) {
    state.token = token
  },

  userId (state, token) {
    state.token = token
  },

  userInfo (state, userInfo) {
    state.userInfo = userInfo
  },

  addKeepAlive (state, item) {
    if (state.keepAlivePages.indexOf(item) === -1) { state.keepAlivePages.push(item) }
  },
  removeKeepAlive (state, item) {
    const index = state.keepAlivePages.indexOf(item)
    if (index > 0) { state.keepAlivePages.splice(index, 1) }
  }

}

var actions = {
  addKeepAlive (state, item) {
    state.commit('addKeepAlive', item)
  },
  removeKeepAlive (state, item) {
    state.commit('removeKeepAlive', item)
  },
  clearAllKeepAlive (state, item) {
    console.log('清空缓存页面')
    state.keepAlivePages = []
  },

  clearWhenExit (state, item) {
    state.commit('token', null)
    state.commit('userId', null)
    state.commit('userInfo', null)
  }

}

/// 只持久化全局数据
var globalStatePersisted = createPersistedState({
  reducer (val) {
    return {
      isMobile: val.isMobile, // 判断是pc还是mobile
      isInApp: val.isInApp, // 是否集成到App中
      token: val.token, // token用于pc验证
      userId: val.userId, // 用户ID
      userInfo: val.userInfo // 用户信息
    }
  }
})

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters: {
    keepAlivePages (content) {
      return content.keepAlivePages
    }
  },
  modules: {
  },
  plugins: [globalStatePersisted]
})
