import axios from 'axios'
import store from '@/store/index'
import router from '@/router/index'

import { Dialog } from 'vant'
import qs from 'qs'

export const url = 'http://39.98.142.33:9000/app/'
const $axios = axios.create({
  baseURL: url
})

// eslint-disable-next-line no-unused-vars
function getToken () {
  return store.state.token
}

// eslint-disable-next-line no-unused-vars
function getUserId () {
  return store.state.userId
}

async function $fetch (url, data = null, method = 'get') {
  if (method.toLowerCase() === 'get' && data != null) {
    data = {
      BASEFLAG_AjaxRequestParams: data
    }
  }

  const isGet = method.toLowerCase() === 'get'

  var response = {}
  try {
    const result = await $axios({
      method,
      url,
      params: isGet ? data : null,
      data: isGet ? null : qs.stringify(data),
      headers: {
        ContentType: 'application/x-www-form-urlencoded',
        Accept: 'application/json',
        token: getToken(),
        userId: getUserId()

      }
    })

    if (result.status >= 200 && result.status < 300) {
      if (result.data != null) {
        if (result.data.code === 0) {
          response.success = true
          response.msg = '成功'
          response.data = result.data.data
          response.code = 0
        } else {
          if (result.data.code === 93 || result.data.code === 92) {
            store.dispatch('clearWhenExit', null)
            /// token 过期
            Dialog.alert({
              title: '提示',
              message: '您的账号信息已过期,请重新登录'
            }).then(() => {
              router.replace('login')
            })
          }
          if (result.data.code === undefined) {
            response.success = false
            response.msg = '请求失败,请重试'
            response.code = result.status
          } else {
            response.success = false
            response.msg = result.data.msg
            response.code = result.data.code
          }
        }
      } else {
        response.success = false
        response.msg = '请求失败,请重试'
        response.code = result.status
      }
    } else {
      response.success = false
      response.msg = '请求失败,请重试'
      response.code = result.status
    }
  } catch (error) {
    response.success = false

    response.msg = '请求失败,请重试'
    response.code = -1000

    console.log(error)
  }
  console.log(response)
  return response
}
/// 登录请求
export const login = (account, password) => $fetch('v1/AppAjax.do?BASEFLAG_ServiceName=LoginService&BASEFLAG_MethodName=login', JSON.stringify([account, password, true, 100000030]))
