/// 获取学习列表
export const studyList = (type, page, pagesize) => $fetch('v1/AppDataListQuery.do?BASEFLAG_ServiceName=StudyService&BASEFLAG_MethodName=study',
  {
    start: (page * pagesize),
    BASEFLAG_PageSize: pagesize,
    BASEFLAG_AppendValues: '[{type:' + type + '}]',
    BASEFLAG_GridBeanValues: '{paging:true}'
  }
  , 'post')

